.why-us {
  height: 100%;
  width: 100%;
  margin: 0 auto;
  background-color: #f0f0f2;
  &__item-wrapper {
    width: 100%;
    height: 100%;
    margin: 70px 0 30px 0;
    @include display-flex(row,center, center);
    flex-wrap: wrap;
  }
  &__item {
    height: 100%;
    width: 25%;
    @include display-flex(column,center, center);
    margin-bottom: 40px;
    text-align: center;
    @media (max-width: 900px) {
      width: 50%;
    }
    @media (max-width: 600px) {
      width: 100%;
      &:nth-of-type(4) {
        margin-bottom: 0;
      }
    }
  }
  
  &__image-wrapper {
    @include display-flex(row,center, center);
    border-radius: 50%;
    border: 1px solid #d4d4d5;
    height: 136px;
    width: 136px;
    background-color: $white;
    margin-bottom: 30px;
  }
  &__item-title {
    font-weight: 600;
    font-size: 16px;
    max-width: 220px;
    line-height: 1.4;
    
  }
}