.pop {
  // margin-bottom: 200px;
  position: relative;
  &__form {
    display: none;
    opacity: 0;
    z-index: 10;
    position: fixed;
    top: 50%;
    left: 47%;
    transform: translateX(-50%) translateY(-50%);
    width: 90%;
    max-width: 352px;
    height: 350px;
    margin: 10px;
    // display: flex;
    flex-direction: column;
    text-align: center;
    padding: 40px 30px;
    background-color: #fff;
    bottom: 70px;
    @media (max-width: 450px) {
      width: 88%;
    }  
  } 
  
  &__overlay {
    z-index: 9;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: black;
    opacity: 0.9;
    display: none;
  }
  &__form-title {
    text-transform: uppercase;
    font-size: 24px;
    font-weight: 200;
    text-align: center;
    margin-bottom: 20px;
  }
  &__item {
    max-width: 290px;
    width: 100%;
    padding: 10px;
    text-align: center;
    margin-bottom: 10px;
    border: solid 1px rgba(193, 179, 179, 0.898);
    
  }
  &__form-text {
    resize: none;
    height: 75px;
  }
  &__button {
    max-width: 290px;
    width: 100%;
    font-size: 14px;
    text-align: center;
    padding: 17px 30px; 
    color: white;
    text-transform: uppercase;
    font-weight: 600;
    white-space: nowrap;
    background:$button;
    border: 1px none;
    outline: none;
    
    &:hover {
      background: $button;
      color: $white;
    }
  }
  &__close {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    background: $button;
    position: absolute;
    bottom: 94%;
    left: 94%;
    cursor: pointer;
    &::before {
      position: absolute;
      content: "";
      top: 46%;
      left: 20%;
      width: 24px;
      height: 2px;
      transform: rotate(-45deg);
      background-color: white;
    }
    &::after {
      position: absolute;
      content: "";
      top: 46%;
      left: 20%;
      width: 24px;
      height: 2px;
      transform: rotate(45deg);
      background-color: white;
    }
  }

}