body {
  min-width: 300px;
  position: relative;
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  color: $base-text-color;
  line-height: 1;
  overflow-x: hidden;
  &.hidden {
      overflow: hidden;
  }
}

input {
  // required: valid;
  outline: none;
  @include transition(0.5s);
  // &:focus {
  // }
}

// textarea {
//   // required: valid;
//   // color: $base-text-color;
//   // &:focus {
//   //   required: invalid;
//   //   color: $base-text-color;
//   // }
// }

button,
input[type='submit'] {
  cursor: pointer;
  @include transition(0.2s);
  // background: transparent;
  // border: 1px solid $base-text-color;
  &:focus,
  &:active {
      outline: none;
  }
}

a {
  text-decoration: none;
  color: $base-text-color;
  cursor: pointer;
  @include transition(0.2s);
  &:hover {
      text-decoration: none;
  }
}

li {
  list-style: none;
}

main {
  background: #ececec;
}


.section__container {
  width: 100%;
  max-width: 1120px;
  padding: 0 15px;
  margin: 0 auto;
  @media (max-width: 400px) {
    padding: 0 10px;
  }
}
.section__title {
  text-transform: uppercase;
  padding-top: 80px;
  font-size: 32px;
  font-weight: 100;
  text-align: center;
  margin-bottom: 20px;
}

.section__subtitle {
  // text-transform: uppercasae;
  font-size: 16px;
  font-weight: 600;
  text-align: center;
  margin-bottom: 70px;
  line-height: 1.2;
}