.portfolio {
  max-width: 1600px;
  margin: 0 auto;
  overflow: hidden;
  &__wrapper{
    @include display-flex(row, center,center);
    flex-wrap: wrap;
    // @media (max-width: 600px) {
    //   flex-direction: column;
    // }
  }
  &__item{
    margin-top: -4px;
    max-width: 30%;
    height: auto;
    background-size: cover;
    position: relative;
    cursor: pointer;
    &:hover {
      opacity: 1;
    }
    &:nth-of-type(1) {
      max-width: 20%;
      @media (max-width: 600px) {
        max-width: 50%;
      }
    }
    &:nth-of-type(4) {
      max-width: 20%;
      @media (max-width: 600px) {
        max-width: 50%;
      }
    }
    &:nth-of-type(5) {
      max-width: 20%;
      @media (max-width: 600px) {
        max-width: 50%;
      }
    }
    &:nth-of-type(8) {
      max-width: 20%;
      @media (max-width: 600px) {
        max-width: 50%;
      }
    }
    @media (max-width: 600px) {
      max-width: 50%;
    }

  }
  &__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &__hover {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0;
    overflow: hidden;
  &:hover {
    opacity: 0.7;
  }
  }
  &__hov_up {
    position: relative;
    height: 60%;
    width: 100%;
    top: 0;
    left: 0;
    background: $button;
    text-align: center;
  }
  &__hov_down {
    @include display-flex(column, center,center);
    position: relative;
    bottom: 0;
    left: 0;
    height: 40%;
    width: 100%;
    background: #ebebed;
    text-align: center;
    color: #333;
    & h4 {
      font-size: 1.2sem;
      margin-bottom: 5px;
    }
  }
  &__link {
    position: absolute;
    top: 50%;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: #fff url(../img/link-hover.png) no-repeat center center;
    margin: -25px 0 0 -25px;
  }
}
