.price {
  height: 100%;
  width: 100%;
  margin-bottom: 30px;
  &__wrapper {
    @include display-flex(row,space-around, center);
    flex-wrap: wrap;
  }
  &__item {
    margin: 20px ;
    width: 290px;
    height: 580px;
    border: 2px solid #f0f0f2 ;
    &--big {
      
      height: 620px;
    }
    
  }
  &__item-title {
    font-size: 24px;
    text-align: center;
    font-weight: 200;
    padding: 40px 0;
    background-color: #f0f0f2;
    margin-bottom: 32px;
    &--big {
      background: $button;
    }
  }
  &__cost {
    @include display-flex(row,center, center);
  }
  &__cost-big {
    font-size: 54px;
    font-weight: 600;
  }
  &__small-top {
    font-size: 30px;
    font-weight: 600;
  }
  &__small-bottom {
    font-size: 12px;
    color: $blue;
    text-transform: uppercase;
    font-weight: 600;
    margin-top: 6px;
  }
  &__dash {
    border-bottom: 3px solid $blue;
    width: 70px;
    margin-left: 46px;
    margin-top: 44px;
    margin-bottom: 44px;
  }
  &__ul {
    margin-left: 46px;
    font-weight: 200;
    margin-bottom: 108px;
    &--premium {
      margin-bottom: 80px;
    }
  }
  &__li {
    margin-bottom: 22px;
    line-height: 1.4;
  }
  &__button {
    font-size: 14px;
    text-align: center;
    padding: 17px 30px; 
    margin-left: 46px;
    text-transform: uppercase;
    font-weight: 600;
    white-space: nowrap;
    background: transparent;
    border: 1px solid black;
    outline: none;  
    &--big {
      background: $button;
    }
    &:hover {
      background: $button;
      color: $white;
    }
  }








}



