.map {
  position: relative;
  // margin-bottom: 200px
  &__form {
    width: 90%;
    max-width: 353px;
    min-height: 320px;
    margin: 10px;
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 40px 30px;
    background-color: #fff;
    position: absolute;
    bottom: 70px;

  } 
  &__form-title {
    text-transform: uppercase;
    font-size: 24px;
    font-weight: 200;
    text-align: center;
    margin-bottom: 20px;
  }
  &__item {
    padding: 10px;
    text-align: center;
    margin-bottom: 10px;
    border: solid 1px rgba(193, 179, 179, 0.898);
  }
  &__button {
    font-size: 14px;
    text-align: center;
    padding: 17px 30px; 
    color: white;
    text-transform: uppercase;
    font-weight: 600;
    white-space: nowrap;
    background:$button;
    border: 1px none;
    outline: none;  
    &:hover {
     border-radius: 15px;
    }
  }
}



