

.header {
  width: 100%;
  height: 70px;
  background-color: #fff;
  position: fixed;
  margin: 0 auto;
  z-index: 5;
  @media (max-width: 920px) {
    @include display-flex(row,space-between, center);
    
    }
  // @media (max-width: 400px) {
  //   position: absolute;
  // }
  
  &__container {
    @include display-flex(row,space-between, center);
    position: relative;
  }
  &__logo {
    display: block;
    height: 40px;
    width: 128px;
    margin-right: 10px;
    background: url('./../img/logo.png') no-repeat center center;
    flex-shrink: 0;
  }
  &__hamb-button {
    width: 24px;
    height: 20px;
    display: none;
    cursor: pointer;
    outline: none;
    -webkit-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
    @media (max-width: 920px) {
    display: block;
    }
  }
  &__hamb-button span {
    display: block;
    width: 24px;
    height: 2px;
    background-color: #0e2646;
    margin-bottom: 6px;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  &__nav {
    @include display-flex(row,space-between, center);
    text-transform: uppercase;
    background-color: #fff;
    @media (max-width: 920px) {
      position: absolute;
      background-color: rgba(0, 0, 0, 0.493);
      width: 100%;
      flex-direction: column;
      top: 54px;
      left: 50%;
      transform: translateX(-50%);
      display: none;
    }
    @media (max-width: 600px) {
      top: 58px;
     }
    &--open {
      @media (max-width: 920px) {
       display: block;
      }
    }
  }
  &__li {
    color: black;
    border-bottom: 2px solid transparent;
    &--active {
      background-color:#f5f6f9;
      border-bottom: solid 2px $blue;
    }
    &:hover {
      background-color:#f5f6f9;
      border-bottom: solid 2px $blue;
      &:nth-of-type(1) {
        background-color:$white;
        border-bottom: solid 2px transparent;
        color:black;
      }
    }
  }
  &__nav-item {
    white-space: nowrap;
    cursor: pointer;
    padding: 27px 18px;
    font-size: 14px;
    font-weight: 200;
    @include display-flex(row,center, center);
    &--home{
      color:$blue;
    }
    @media (max-width: 920px) {
      width: 100%;
      padding: 7px 18px;
      font-weight: 400;
    }
   

  }
  &__contact {
    @include display-flex(row,center, center);
    @media (max-width: 600px) {
      flex-direction: column;
      align-items: flex-end;
    }
  }
  &__phone-wrapper {
    font-size: 12px;
    font-weight: 600;
    margin: 10px;
    @include display-flex(row,space-between, center);
    white-space: nowrap;
    @media (max-width: 600px) {
      margin: 6px 10px;
    }
  }
  &__fa {
    margin-right: 8px;
    color: $gray;
  }
  &__button {
    font-size: 12px;
    text-align: center;
    height: 48px;
    margin: 10px;
    padding: 0 10px;
    text-transform: uppercase;
    font-weight: 600;
    color: $white;
    white-space: nowrap;
    background: $button;
    border: none;
    outline: none;  
    @media (max-width: 1200px) {
      position: absolute;
      top: 60px;
      left: 72%;
    }
    @media (max-width: 920px) {
     display: none;
    }
    &:hover {
      border-radius: 15px;
    }
  
    
  }

}