.slider {
  width: 100%;
  height: 100%;
  max-height: 460px;
  @include display-flex(row,center, center);

  &__item {
    width: 100%;
    padding: 30px 0;
    height: 100%;
    background: url('../img/slider-img-1.png') no-repeat center;
    background-size: cover;
    @include display-flex(row,center, center);
    &--2 {
      background: url('../img/slider-img-2.png') no-repeat center;
    }
    &--3 {
      background: url('../img/slider-img-3.png') no-repeat center;
    }
  }
  &__image-wrapper {
    width: 300px;
    height: 250px;
    margin-right: 25px;
    @include display-flex(row, flex-end,center);
    @media (max-width: 900px) {
      width: 210px;
      height: 175px;
    }
    @media (max-width: 900px) {
      width: 145px;
      height: 122px;
    }
  }
  &__image {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  &__text-wrapper {
    margin: 20px 0;
    width: 40%;
    max-width: 450px;
    color: $white;
   
  }
  &__title {
    max-width: 374px;
    font-size: 30px;
    text-transform: uppercase;
    font-weight: 600;
    margin-bottom: 40px;
    line-height: 1.2;
    @media (max-width: 900px) {
      font-size: 18px;
      margin-bottom: 20px;
    }
    @media (max-width: 600px) {
      font-size: 14px;
    }
  }
  
  &__subtitle {
    font-size: 20px;
    font-weight: 100;
    line-height: 1.2;
    @media (max-width: 900px) {
      font-size: 14px;
    }
    @media (max-width: 600px) {
      font-size: 12px;
    }
  }

  &__btn {
    background: transparent;
    outline: none;
    border: solid 1px white;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    font-size: 36px;
    color: #fff;
    z-index: 6;
    @include display-flex(row, center,center);
    &:hover {
      background-color: $blue;
      @media (max-width: 600px) {
         background: transparent;
      }
    }
    @media (max-width: 600px) {
      width: 30px;
      height: 30px;
      font-size: 32px;
      border: none;
    }

    &--next {
      position: absolute;
      content: '';
      top: 50%;
      left: 90%;
      transform: translateY(-50%);
    }
    &--prev {
      position: absolute;
      content: '';
      top: 50%;
      right: 90%;
      transform: translateY(-50%);
    }
  }
  



}

