.main-screen {
 width: 100%;
 height: 100vh;
 background: url('../img/bg.png') no-repeat ;
 background-size: cover;
 @include display-flex(column,center, center);

  &__text-wrapper {
  margin-top: 120px;
  @include display-flex(column,center, center);
  text-align: center;
  color: $white;
  text-transform: uppercase;
  }
  &__title {
    font-size: 30px;
    font-weight: 600;
    margin-bottom: 22px;
    @media (max-width: 900px) {
      font-size: 22px;
    }
  }
  &__subtitle {
  font-size: 40px;
  font-weight: 200;
  margin-bottom: 22px;
    @media (max-width: 900px) {
      font-size: 26px;
    }
  }
  &__price {
  font-size: 48px;
  margin-bottom: 30px;
  font-weight: 600;
  border: solid white 1px;
  background-color: rgba(255, 255, 255, 0.24);
  padding: 22px 54px;
  @media (max-width: 900px) {
    font-size: 30px;
  }
  }
  &__button {
    font-size: 14px;
    text-align: center;
    margin: 10px;
    padding: 20px;
    text-transform: uppercase;
    font-weight: 800;
    color: $white;
    white-space: nowrap;
    background: $button;
    border: none;
    outline: none;  
    &:hover {
      border-radius: 15px;
    }
  }
}